<template>
  <div class="myitem">
    <input type="checkbox" :checked="item.isCheck" class="to-lable" />
    <label for="" @dblclick="edit($event)" v-if="!item.isEditing">{{
      item.label
    }}</label>
    <input
      class="todoitem"
      type="input"
      v-if="item.isEditing"
      :value="item.label"
      ref="input"
      @blur="changes"
    />
  </div>
</template>

<script>
export default {
  name: "MyItem",
  props: {
    item: {
      type: Object,
    },
    changeEdit: {
      type: Function,
    },
    changedata: {
      type: Function,
    },
  },
  beforeUpdate() {
    console.log("yuyuyuyuyuyuyuyuyuyuyuyuyuyu");
  },
  methods: {
    edit() {
      this.changeEdit(this.item);

      this.$nextTick(() => {
        this.$refs.input.focus();
      });
    },
    changes() {
      this.changedata(this.item, this.$refs.input.value);
      this.changeEdit(this.item);
    },
  },
};
</script>

<style scoped>
.myitem {
  font-size: 30rem;
}

label {
  user-select: none;
}

.to-lable {
  width: 30rem;
  height: 30rem;
}

input {
  margin: 10rem;
  padding: 0;
  vertical-align: middle;
}



.todoitem {
  width: 80%;
  height: 30rem;
  font-size: 30rem;
  outline: none;
}
</style>
