//该文件用于创建vuex中最为核心的store
import Vue from "vue"
import Vuex from 'vuex'

Vue.use(Vuex)


const numberOption = {
    namespaced:true,
    actions: {
        getInfo(state, n) {

            setInterval(function () {
                state.commit('JIA', n)
            }, 2000)

        }
    },
    mutations: {
        JIA(state, n) {
            state.count += n
        }
    },
    state: { count: 1 },
    getters: {
        GETC(state) {
            return state.count * 10
        }
    }
}

export default new Vuex.Store({
    modules: {
        numberAbout: numberOption
    }
})