<template>
  <div>
    <template v-for="item in itemdata">
      <my-item :item="item" :key="item.itemdata" :changeEdit="changeEdit" :changedata="changedata"></my-item>
    </template>
  </div>
</template>

<script>
import { nanoid } from "nanoid";

import MyItem from "./MyItem.vue";

export default {
  name: "MyContent",
  data() {
    return {
      itemdata: [
        {
          isCheck: true,
          isEditing:false,
          label: "rrf",
          id: nanoid(),
        },
        {
          isCheck: false,
          label: "ffff",
          isEditing:false,
          id: nanoid(),
        },
      ],
    };
  },
  methods:{
      changeEdit(item){
          this.itemdata.forEach((todo)=>{
            
            if(item.id==todo.id)
            {
                todo.isEditing = !todo.isEditing
            }
          })
      },
    changedata(item,label){

        if(label==""){
            alert("label不为空")
            return
        }

          this.itemdata.forEach((todo)=>{
            
            if(item.id==todo.id)
            {
                todo.label = label
            }
          })
      }
  },
  components: {
    MyItem,
  },
};
</script>

<style></style>
